
@import (less) "color.less";

.header {
    //position: relative;
    //top: 0;
    //left: 0;
    width: 100%;
    background-color: @lightest_gray;
    box-shadow: 0 0 1.250em -0.625em rgba(0, 0, 0, 0.8);
    .navbar {
        position: relative;
        border: 0 !important;
        border-radius: 0 !important;
        margin: 0 !important;
        left: 0;
        right: 0;
        width: 100%;
        background-color: @green;
        .container {
            display: table;
            position: relative;
            padding-right: 0;
            background-color: @green;
            height: 4.375em;
        }
        .container-slim {
            height: 3.125em !important;
        }
    }
    .row {
        margin: 0;
    }
}

.header-overlay {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    //background: rgba(255, 255, 255, 0.7);
    background: @lightest_gray;
}
.navbar {
    .container {
        .navbar-logo {
            display: table-cell;
            vertical-align: middle;
            width: 17em;
        }
        .navbar-toggle {
            display: table-cell;
            vertical-align: middle;
            width: 5.000em;
            height: 4.375em;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            margin: 0;
            padding: 0;
            text-align: center;
            cursor: pointer;
            &:hover {
                background: @darkest_gray;
            }
            &:focus {
                background: @darkest_gray;
            }
            &:active {
                background: @darkest_gray;
            }
        }
        .navbar-info-cliente {
            display: table-cell;
            vertical-align: middle;
            width: 22.25em;
            height: 3.125em;//4.375em;
            background-color: transparent;
            // border-left: 1px solid @white;
            // border-right: 1px solid @white;
            text-align: left;
            color: @white;
            line-height: 1em;
            //padding-top: 0.2em;//0.625em;
            .container-arrow {
                float: right;
                padding-top: 1.25em;
                padding-right: 1.5625em;
            }
            .accordion-arrow{
                cursor: pointer;
                font-size: 1.6em;
            }
            .info-cliente-open {
                position: absolute;
                background-color: @darkest_gray;
                z-index: 1000;
                width: 22.25em;
                height: 9.8125em;
                border: 0;
                border-radius: 0;
                text-align: center;
                margin-top: 0.5625em;
                display: table;
                .container-nsg-title {
                    vertical-align: middle;
                    height: 2.3125em;
                    display: table-cell;
                }
                .container-nsg-cliente {
                    vertical-align: middle;
                    height: 3.75em;
                    border-top: 1px solid @gray;
                    display: table-cell;
                    cursor: pointer;
                    text-align: left;
                }
                .container-nsg-cliente-select {
                    vertical-align: middle;
                    height: 3.75em;
                    border-top: 1px solid @gray;
                    display: table-cell;
                    cursor: pointer;
                    text-align: left;
                    background-color: #3f3f3f;
                    font-weight: bold;
                    .icon {
                        float: right;
                        color: @green;
                        padding-right: 10px;
                    }
                }
                .title-nsg-label {
                    color: @light_gray;
                     text-transform: uppercase;
                }
                .nsg-cliente {
                    margin-left: 1em;
                    color: @light_gray;
                }
                .nsg-cliente-selected {
                    margin-left: 1em;
                    color: @white;
                }
            }
        }        
        .navbar-hole {
          display: table-cell;
          vertical-align: middle;
          margin-top: 5px;
          margin-bottom: 5px;
        }
    }

}
.navbar-logo-brand {
    display: block;
    width: 250px;//200px;
    height: 30px;//25px;
    background-image: url(/cjlib/assets/images/sprite.png);
    background-repeat: no-repeat;
}

.navbar-logo-brand-small {
    display: block;
    width: 225px;//143px;
    height: 25px;//25px;
    // background-size: 225px;//143px;
    background-image: url(/cjlib/assets/images/sprite.png);
    background-repeat: no-repeat;
}
.navbar-toggle {
    button {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        border: none;
        background: none;
        outline: 0;
    }
    .icon-bar {
        width: 1.875em;
        height: 0.125em;
        margin: 0.313em auto;
        background-color: @white !important;
    }
    .ico-navbar-menu,
    .ico-comuni-chiudi {
        color: @white;
        font-size: 1.500em;
        margin: 0.313em auto;
    }
}
.navbar-toggle-operation {
    button {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        border: none;
        background: none;
        outline: 0;
    }
    .icon-bar {
        width: 1.875em;
        height: 0.125em;
        margin: 0.313em auto;
        background-color: @white !important;
    }
    .icomoon-Modifica_matita {
        color: @white;
        font-size: 1.500em;
        margin: 0.313em auto;
    }
    span {
        &.labelOperazioni {
            color: @white;
            font-size: 0.750em;
        }
    }
}
.navbar-toggle-open {
    background: @darkest_gray !important;
}
.nome-applicativo {
    position: relative;
    font-size: 1.5em;
    margin-bottom: -0.417em;
    color: @white;
    font-weight: normal;
}
.header-progress-container,
.header-paging-container {
    border-bottom: 1px solid @light_gray;
}
@media (max-width:768px) {
    .navbar {
        .container {
            width: 100%;
        }
    }
}
// TODO è NECESSARIO AGGIORNARE L'URL DELLA IMMAGINE, IN ACCORDO CON LA RISOLUZIONE DEI PERCORSI PER I PACCHETTI NPM
@media (-webkit-min-device-pixel-ratio:2) {
    .navbar-logo-brand {
        background-image: url(/cjlib/assets/images/sprite-x2.png);
    }
}
// TODO è NECESSARIO AGGIORNARE L'URL DELLA IMMAGINE, IN ACCORDO CON LA RISOLUZIONE DEI PERCORSI PER I PACCHETTI NPM
@media (min-resolution:192dpi) {
    .navbar-logo-brand {
        background-image: url(/cjlib/assets/images/sprite-x2.png);
    }
}
.triangolo {
    width    : 0;
    height   : 0;
    z-index  : 100;

    border-left   : 0.625em solid transparent;
    border-right  : 0.625em solid transparent;
    border-bottom : 0.625em solid @darkest_gray;
    float: right;
    margin-right: 1.75em;
}
.triangolo-hide {
    width    : 0;
    height   : 0;
    z-index  : 100;

    border-left   : 0.625em solid transparent;
    border-right  : 0.625em solid transparent;
    border-bottom : 0.625em solid transparent;
    float: right;
}
.container-row {
    display: table-row;
}
.container-ident-cliente {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.875em;
    color: inherit;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    width:78%;
    padding-left: 0.625em;
    font-weight: normal;
    float: left;
}
.voce-ident-cliente {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.875em;
    color: inherit;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    font-weight: normal;
}
.testo-ident-cliente {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 0.875em;
    color: inherit;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    font-weight: bold;
}
