//     }
// }
@media (max-width:970px) {
    .nome-applicativo {
        font-size: 1.2em;
    }
}


.contrasto-on {
    .header {
        background-color: @white;
        .navbar {
            .container {
                background-color: @black;
            }
        }
        .navbar-tape {
            background-color: @black;
        }
    }
    .header-overlay {
        background: rgba(255, 255, 255, 1);
    }
    .navbar {
        .container {
            .navbar-toggle {
                background-color: transparent;
                &:hover {
                    background: @black;
                }
                &:focus {
                    background: @black;
                }
                &:active {
                    background: @black;
                }
            }
            .navbar-toggle-operation {
                background-color: transparent;
                &:hover {
                    background: @white;
                    color: @black;
                    .icomoon-Modifica_matita {
                        color: @black;
                    }
                    span {
                        color: @black;
                    }
                }
                &:focus {
                    background: @white;
                    color: @black;
                    .icomoon-Modifica_matita {
                        color: @black;
                    }
                    span {
                        color: @black;
                    }
                }
                &:active {
                    background: @white;
                    color: @black;
                    .icomoon-Modifica_matita {
                        color: @black;
                    }
                    span {
                        color: @black;
                    }
                }
                &.navbar-toggle-open {
                    background: @white !important;
                    color: @black;
                    .icomoon-Modifica_matita {
                        color: @black;
                    }
                    span {
                        color: @black;
                    }
                }
            }
            .navbar-info-cliente {
                background-color: transparent;
                border-left: 1px solid @white;
                border-right: 1px solid @white;
                color: @white;
                .info-cliente-open {
                    .container-nsg-title {
                        border-bottom: 1px solid @white;
                        border-top: 1px solid @white;
                    }
                    background-color: @black;
                    .container-nsg-cliente {
                        border-bottom: 1px solid @white;
                        border-top: none;
                    }
                    .container-nsg-cliente-select {
                        border-bottom: 1px solid @black;
                        border-top: none;
                        background-color: @white;
                        .icon {
                            color: @black;
                        }
                    }
                    .title-nsg-label {
                        color: @white;
                    }
                    .nsg-cliente {
                        color: @white;
                    }
                    .nsg-cliente-selected {
                        color: @black;
                    }
                }
            }
        }
    }
    .navbar-toggle {
        .icon-bar {
            background-color: @white !important;
        }
        .ico-navbar-menu,
        .ico-comuni-chiudi,
        .icomoon-Modifica_matita {
            color: @white;
        }
    }
    .navbar-toggle-open {
        background: @black !important;
    }
    .prev-page-btn-container {
        .prev-page-btn {
            color: @black;
        }
    }
    .page-title-container {
        border-bottom: 1px solid @black;
    }
    .nome-applicativo {
        color: @white;
    }
    .header-progress-container,
    .header-paging-container {
        border-bottom: 1px solid @black;
    }
    .triangolo {
        position: absolute;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        content: " ";
        width: 15px;
        height: 15px;
        border: 0 solid white;
        border-width: 1px 0 0 1px;
        background: #000000;
        right: 112px;
        z-index: 1001;
        bottom: -8px;
        margin: 0 auto;
    }
    .container-ident-cliente {
        color: inherit;
    }
    .voce-ident-cliente {
        color: inherit;
    }
    .testo-ident-cliente {
        color: inherit;
    }
}
